@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap');

//Colors
$black: #121212;
$white: #FFFFFF;
$buttonBg: #E4C1AE;

//Fonts
$bodyFont: "Roboto", sans-serif;


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;

  }

body {
    font-family: $bodyFont;
    background: $white;
    display: flex;
    justify-content: center;
    font-size:1.6rem;

    h1,h2{
        font-weight: 700;
        font-size: 4.2rem;
    }
}

main {
    max-width:1440px;
    margin-top:96px;
    display: flex;
    justify-content: center;

    .mainPage{
        display: flex;
    }

    .rightSide {
        margin-left: 95px;
        display: flex;
        align-items: center;

        .centerRightSide {
            max-width: 380px;

            p{
                padding: 24px 0 32px;
                line-height: 2.4rem;
            }

            .modalButton{
                background-color: $buttonBg;
                color: $white;
                padding:16px 40px;
                border-radius: 32px;
                border:none;
                font-weight: 700;

                &:hover, &:focus{
                    outline: 1px solid $black;
                }
            }
        }
    }
}

.modalSection {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    padding-top: 300px; //need to change to proper center modal (temporary solution)
    z-index: 1;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba($black, 0.5);

    .modalContent {
        background-color: $white;
        margin: auto;
        max-width: 920px;
        max-height: 210px;

        p{
            font-size: 1.6rem;

            span{
                font-weight: bold;
            }
        }

        .closeModal {
            color: $black;
            text-align: right;
            font-size: 2.4rem;

            &:hover, &:focus {
                color: $black;
                cursor: pointer;
            }
        }
    }
  }